<template>
  <div>
    <BasePanel>
      <template #panel-head>
        <router-link to="/">主页</router-link>
        /
        <span>发布话题</span>
      </template>
      <template #panel-content>
        <span>请选择板块：</span>
        <el-select v-model="tab" placeholder="请选择">
          <el-option label="分享" value="share">分享</el-option>
          <el-option label="招聘" value="job">招聘</el-option>
          <el-option label="问答" value="ask">问答</el-option>
          <el-option label="客户端测试" value="dev">客户端测试</el-option>
        </el-select>
        <el-input v-model.trim="title" placeholder="标题字数 10 字以上" />
        <!-- 输入框 -->
        <div class="markdown-container">
          <div class="container">
            <mavon-editor ref="md" style="min-height: 400px" v-model.trim="content" />
            <el-button class="editor-btn" type="primary" @click="submit" plain>提交</el-button>
          </div>
        </div>
      </template>
    </BasePanel>
  </div>
</template>

<script>
import BasePanel from "../components/layout/BasePanel.vue";
import {create} from '../plugins/api'
export default {
  components: { BasePanel },
  data() {
    return {
      tab: "",
      title:'',
      content:'',
      markdownOption: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
      },
    };
  },
  methods: {
    async submit() {
      const {tab, content,title} = this
      const accesstoken = sessionStorage.getItem("accesstoken")
      const res =  await create(accesstoken,title,tab,content)
      this.$router.push(`/topic/${res.topic_id}`)
            this.$notify({
          title: '发布成功',
          message: '跳转到文章页'
        });
    }
  }
};
</script>

<style lang='less'>
 .markdown-container {
    .editor-btn {
      margin-top: 20px;
    }
    .title {
      padding-bottom: 30px;
      text-align: center;
      font-size: 20px;
      letter-spacing: 1px;
      color: #333;
      font-weight: 600;
    }
  }
</style>